@use '../util' as *;

.container {
	padding: 0 rem(26);
    max-width: rem(1122);
	margin: 0 auto;
}

.promo {
    background: var(--promo) url("../images/background.png") no-repeat 50% 100% / 250vh;
    height: 100vh;

	@include breakpoint-down(medium) {
        background-position: 50% -150%;
        background-size: 150vh;
        height: rem(900);
	}
}

.swiper {
    cursor: grab;
    padding-bottom: rem(15);

    &-wrapper {
        max-width: rem(472);
        text-align: center;
    }

    &-pagination-bullet {
        width: rem(11);
        height: rem(11);
        background-color: var(--client);
    }
}

button, .button {
    color: var(--white);
	background-color: var(--tomato);
    border-bottom: rem(1) solid var(--border);
	border-radius: rem(22);
    padding: rem(9) rem(32) rem(12);
}

button {
	font-size: rem(18);
    cursor: pointer;
    border-width: 0;

    @include breakpoint-down(medium) {
        display: none;
    }
}

.button__input {
    position: relative;
    top: rem(3);
    right: rem(-12);
    border-radius: 0 rem(45) rem(45) 0;
    background-color: var(--blue);
    padding: rem(18) rem(20);

    @include breakpoint-down(medium) {
        top: rem(2);
        right: rem(-18);
        padding: rem(7.5) rem(10);
    }

    @include breakpoint-down(small) {
		right: rem(0);
        padding: rem(3.5) rem(5);
    }
}

// === Hamburger Fade ===
.has-fade {
    visibility: hidden;
}

@keyframes fade-in {
    from {
        visibility: hidden;
        opacity: 0;
    }

    1% {
        visibility: visible;
        opacity: 0;
    }

    to {
        visibility: visible;
        opacity: 1;
    }
}

.fade-in {
    animation: fade-in 200ms ease-in-out forwards;
}

@keyframes fade-out {
    from {
        visibility: visible;
        opacity: 1;
    }

    99% {
        visibility: visible;
        opacity: 0;
    }

    to {
        visibility: hidden;
        opacity: 0;
    }
}

.fade-out {
    animation: fade-out 200ms ease-in-out forwards;
}
// === / Hamburger Fade ===